.fadeTranslate-enter {
  opacity: 0;
}

.fadeTranslate-enter.fadeTranslate-enter-active {
  opacity: 1;
  -webkit-transition: opacity 1000ms ease-in;
  -o-transition: opacity 1000ms ease-in;
  transition: opacity 1000ms ease-in;
}

.fadeTranslate-exit {
  opacity: 1;
  -webkit-transform: translate(0, 0);
      -ms-transform: translate(0, 0);
          transform: translate(0, 0);
}

.fadeTranslate-exit.fadeTranslate-exit-active {
  opacity: 0;
  -webkit-transform: translate(0, 3vh);
      -ms-transform: translate(0, 3vh);
          transform: translate(0, 3vh);
  -webkit-transition: opacity 1000ms ease-in, -webkit-transform 1000ms ease-in-out;
  transition: opacity 1000ms ease-in, -webkit-transform 1000ms ease-in-out;
  -o-transition: opacity 1000ms ease-in, transform 1000ms ease-in-out;
  transition: opacity 1000ms ease-in, transform 1000ms ease-in-out;
  transition: opacity 1000ms ease-in, transform 1000ms ease-in-out, -webkit-transform 1000ms ease-in-out;
}

.fix-container {
